<template>
  <div id="detailDialog" v-if="dialog" v-click-outside="closeDialog">
    <v-row justify="center">
      <v-dialog
        v-model="dialog"
        width="600px"
      >
        <v-card>
          <v-card-title>
            <span class="headline">{{ apiData.statNm }}</span>
          </v-card-title>
          <v-card-text>
             : {{ useTimeSet(apiData.useTime) }}
          </v-card-text>
          <v-simple-table>
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-center">
                    
                  </th>
                  <th class="text-center">
                     
                  </th>
                  <th class="text-center">
                    
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-if="apiData.countOfChger === 1"
                >
                  <td class="text-xs-center">{{ apiData.powerType }}</td>
                  <td class="text-xs-center">{{ charTypeSet(apiData.chgerType) }}</td>
                  <td class="text-xs-center">{{ statSet(apiData.stat).status }}<br>({{ statSet(apiData.stat).reason }})</td>
                </tr>
                <tr
                  v-else-if="apiData.countOfChger > 1"
                  v-for="(item, i) in arrData"
                  :key=i
                >
                  <td class="text-xs-center">{{ item.powerType }}</td>
                  <td class="text-xs-center">{{ charTypeSet(item.chgerType) }}</td>
                  <td class="text-xs-center">{{ statSet(item.stat).status }}<br>({{ statSet(item.stat).reason }})</td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
          <v-card-subtitle>
            
          </v-card-subtitle>
          <v-card-text>
             : {{ apiData.addr }}<br>
             : {{ apiData.busiNm }}<br>
             : {{ apiData.busiCall }}<br>
             : {{ parkingFreeSet(apiData.parkingFree) }}<br>
             : {{ apiData.note }}
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              color="red darken-1"
              text
              @click="closeDialog"
            >
              
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </div>
</template>

<script>
import { dataSetDes } from '@/mixins/dataSetDes'

export default {
  name: 'DetailDialog',
  data() {
    return {
      dialog: false,
      arrData: [],
    }
  },
  props: ['open', 'apiData'],
  watch: {
    open(newVal) {
      this.dialog = newVal
    },
    apiData(newVal) {
      if(newVal.countOfChger > 1) {
        this.arrData = []
        for(let i = 0; newVal.countOfChger > i; i++) {
          this.arrData.push({
            powerType: newVal.powerTypeArr[i],
            chgerType: newVal.chgerTypeArr[i],
            stat: newVal.statArr[i]
          })
        }
      }
    }
  },
  methods: {
    closeDialog: function() {
      this.$emit('close')
    }
  },
  mixins: [dataSetDes]
}
</script>