export const dataSetDes = {
  methods: {
    charTypeSet: val => {
      switch (val) {
        case '01':
          return 'DC'
          break;
        case '02':
          return 'AC'
          break;
        case '03':
          return 'DC+AC3'
          break;
        case '04':
          return 'DC'
          break;
        case '05':
          return 'DC+DC'
          break;
        case '06':
          return 'DC+AC3+DC'
          break;
        case '07':
          return 'AC3'
          break;
        default:
          return 'err'
          break;
      }
    },
    statSet: val => {
      switch (val) {
        case 1:
          return {
            status: '',
            reason: ''
          }
          break;
        case 2:
          return {
            status: '',
            reason: ''
          }
          break;
        case 3:
          return {
            status: '()',
            reason: ''
          }
          break;
        case 4:
          return {
            status: '',
            reason: ''
          }
          break;
        case 5:
          return {
            status: '',
            reason: ''
          }
          break;
        case 6:
          return {
            status: '',
            reason: ''
          }
          break;
        default:
          return {
            status: 'err',
            reason: 'err'
          }
          break;
      }
    },
    parkingFreeSet: val => {
      switch (val) {
        case 'Y':
          return ''
          break;
        case 'N':
          return ''
          break;
        default:
          return ''
          break;
      }
    },
    useTimeSet: val => {
      return (val === '') ? " " : val
    }
  },
}